.WrapDragger {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.DraggerLayout {
    transition: all .15s;
}

body {
    background: white;
}


.layout-Item {
    background: white;
    height: 100%;
    border-radius: 3px;
}

.layout-item:hover {
    cursor: -webkit-grab;
}

.normal-layout {
    background: #d9d9d9;
}
